import React from 'react';

import axios from 'axios';

const test =  {"required":{"":8},"owned":{}};

class RequestView extends React.Component {
    constructor(props) {
        super(props);
        this.data = this.parserInput(props.input);
        this.state = {result: null};
    }
    componentDidMount() {
        if(Object.keys(this.data.required).length===0){
            this.setState({result:'No input'});
            return
        }
        fetch('https://ak.inva.land/plan/', {method:'post',body:JSON.stringify(this.data)})
            .then(res=>res.json())
            .then(res=>this.setState({result:res}))
            .catch(e=>{
            console.log(''+e);
        })
    }

    parserInput(raw) {
        const required = {};
        Object.keys(raw).filter(x => parseInt(raw[x].need) > 0).forEach(
            function (x) {
                required[raw[x].name] = raw[x].need;
            }
        )
        const owned = {};
        Object.keys(raw).filter(x => parseInt(raw[x].have) > 0).forEach(
            function (x) {
                owned[raw[x].name] = raw[x].have;
            }
        )
        // console.log(raw,required,owned);
        return {required: required, owned: owned}
    }


    render() {
        return (
            <div>
                {this.state.result?JSON.stringify(this.state.result):'Loading...'}
            </div>
        )
    }
}

export default RequestView
