import React from "react";
import propTypes from "prop-types";
import {makeStyles, withStyles} from "@material-ui/core";
import TextField from '@material-ui/core/TextField';
import {itemGet, itemCountStyled, classifier, sortByRarity, ItemIcon, itemData} from './itemData'

const styles = makeStyles(theme => ({
    root: {
    },
    headline:{
        margin:'5px 0',
    },
    inner:{
        display: 'flex',
        flexWrap:'wrap',
        margin: '0px 10px 5px 10px',
        '&>div':{
            minHeight:'80px',

            margin:'0 5px',
        },

    },
    itemWrap:{
        display:'flex',
        alignItems:'center',
    },
    textField:{
        margin:'0 5px',
        width:'80px',
    },
    tierWrap:{
        display:'flex',
        alignItems:'baseline',
        whiteSpace: 'nowrap',
    },
    tierWrap_icon:{
        display:'flex',
        alignItems:'flex-start',
        whiteSpace: 'nowrap',
        '&>div:first-child':{
            marginTop:'10px',
        }
    },
    iconWrap:{
        textAlign:'center',
    },
    alpha:{
        opacity:0.5,
    },
}));

const CssTextField = withStyles({
    root: {
        '& label, label.Mui-focused,': {
            color: '#fff',
        },
        '& label': {
            top: '-9px',
        },
        '& fieldset':{
            borderColor: '#fff !important',
        },
        '& input':{
            padding:'10px 15px',
            fontSize:'14px',
            color: '#fff',
        }
    },


})(TextField);

function MyTextField(props) {
    const {value,handler,...other}=props;
    const [update,setUpdate]=React.useState(true);
    function onChangeHandler(event) {
        if(event.target.value ===''){
            handler('');
            setUpdate(!update);
            return
        }
        let temp = parseInt(event.target.value);
        if(temp>=0){
            handler(temp);
            setUpdate(!update);
        }
    }
    return <CssTextField value={value} onChange={onChangeHandler} {...other}/>
}

const rarityList = [5,4,3,2,1,0];
const materialItemList = Object.keys(itemData).filter(x=>itemData[x].type === 'MATERIAL'  && /^MTL_SL/.test(itemData[x].iconId));
const tiers = classifier(materialItemList,'rarity',rarityList);
const isEmpty = x=> x==='0'||x===''||x===0;

function Raw(props) {
    const {value,handler,icon}=props;
    const classes=styles();
    // const [update, setUpdate] = React.useState(0);

    const onChangeHandler = (x,id,key)=> handler(
        {
            ...value,
                [id]:
                    {...value[id],
                        [key]: x
                    }
        })
    return(
        <div>
            <div className={classes.headline}></div>
            {rarityList.map(x=> tiers[x].length>0?(
                <div className={icon?classes.tierWrap_icon:classes.tierWrap}>
                    <div>{`Tier ${x+1}`}</div>
                    <div className={classes.inner}>
                        {tiers[x].map(
                            y=><div >
                                <div className={[classes.itemWrap,(isEmpty(value[y].need)&&isEmpty(value[y].have))?classes.alpha:null].join(' ')}>
                                    {
                                        icon ?
                                            <ItemIcon id={y} style={{width:'60px',height:'60px'}}/>
                                            : itemCountStyled(y)
                                    }
                                    <div>
                                        <MyTextField
                                            className = {[classes.textField,isEmpty(value[y].need)?classes.alpha:null].join(' ')}
                                            id={y}
                                            value={value[y].need}
                                            label={""}
                                            margin="none"
                                            variant="outlined"
                                            handler={e=>onChangeHandler(e,y,'need')}
                                        />
                                        <MyTextField
                                            className = {[classes.textField,isEmpty(value[y].have)?classes.alpha:null].join(' ')}
                                            id={y}
                                            value={value[y].have}
                                            label={""}
                                            margin="none"
                                            variant="outlined"
                                            handler={e=>onChangeHandler(e,y,'have')}
                                        />
                                    </div>
                                </div>
                            </div>
                        )}
                    </div>
                </div>
            ):null)

            }
        </div>
    )
}

Raw.propTypes = {
    value: propTypes.object.isRequired,
    handler:propTypes.func.isRequired,
    icon:propTypes.bool.isRequired,
};

export {materialItemList};
export default Raw;