import React from "react";
import propTypes from "prop-types";
import {makeStyles, withStyles} from "@material-ui/core";
import {data} from './chatacterData';
import skill_table from './excel/skill_table';
import {itemGet, itemCountStyled, classifier, sortByRarity, ItemIcon, itemData} from './itemData'
import {evolveFormula} from "./buildingData";

const styles = theme => ({
    root:{
        width:'100%',
        minHeight:'fit-content',
        filter: 'drop-shadow(0px 4px 4px rgba(7, 7, 7, 0.8))',
        background: '#404040',
        color:'white',
        // borderRadius:'3px',
        // border:'1px solid #000',
        padding:'7.5px 10px',
        // display:'flex',
        '& div':{
            whiteSpace:'nowrap',
            margin:'2.5px 5px',
            display:'inline-block',
        }
    },
    head:{
        borderBottom:'1px solid #000',
        height:'30px',
    },
});

function breakDown(item,count,rarity=null) {
    // Break down single item
    let result = {goldCost:0};
    if (rarity!==null && itemData[item].rarity<rarity){
        return false
    }
    if (item in evolveFormula){
        result.goldCost+=evolveFormula[item].goldCost*count;
        for(let x of evolveFormula[item].costs){
                result[String(x.id)]=x.count*count;
        }
        // console.log([item,count,result]);
        return result;
    }
    return false;
}

function multipleBreakDown(raw,rarity=null) {
    // console.log(raw);
    let result=[];
    let rest = [];
    let totalGoldCost = 0;
    for (let key in raw){
        let temp = breakDown(key,raw[key],rarity);
        // console.log([key,temp])
        if(temp){
            let {goldCost,...others}=temp;
            // console.log(goldCost)
            result.push(others);
            totalGoldCost += goldCost;
        }else {
            rest.push({[key]:raw[key]});
        }
    }

    // console.log({result:mergeCount(result),rest:mergeCount(rest)});
    // console.log( JSON.parse(JSON.stringify({result:mergeCount(result),rest:mergeCount(rest)})));
    // const output={result:mergeCount(result),rest:mergeCount(rest)};
    // console.log(output);
    // console.log(JSON.parse(JSON.stringify(output)));
    return{result:mergeCount(result),rest:mergeCount(rest),goldCost:totalGoldCost}
}

function mergeCount(countList) {
    let result=countList[0];
    for (let list_iter of countList.slice(1)){
        for(let key in list_iter){
            if(result[key]){
                result[key]+=list_iter[key];
            } else {
                result[key]=list_iter[key];
            }
        }
    }
    return result;
};


function costCount(id,rank,skillBasic,skillMaster) {
    const evolveCost = data[id].evolveCost;
    const baseCost = data[id].allSkillLvlup;
    const masterCost = data[id].masterSkillCost;
    let cost ={};
    for (let i=parseInt(rank[0])+1;i<=parseInt(rank[1]);i++){
        if(evolveCost[i]){
            evolveCost[i].forEach(x => {
                if (!cost[x.id]) {
                    cost[x.id] = x.count;
                } else {
                    cost[x.id] += x.count;
                }
            })
        }
    }
    for (let i=parseInt(skillBasic[0]);i<=parseInt(skillBasic[1])-1;i++){
        if(baseCost[i-1]) {
            baseCost[i - 1].forEach(x => {
                if (!cost[x.id]) {
                    cost[x.id] = x.count;
                } else {
                    cost[x.id] += x.count;
                }
            })
        }
    }
    for (let n=0;n<skillMaster.length;n++) {
        if(skillMaster[n].length > 0) {
            for (let i = parseInt(skillMaster[n][0]) + 1; i <= parseInt(skillMaster[n][1]); i++) {
                if(masterCost[n][i - 1]) {
                    masterCost[n][i - 1].forEach(x => {
                        if (!cost[x.id]) {
                            cost[x.id] = x.count;
                        } else {
                            cost[x.id] += x.count;
                        }
                    })
                }
            }
        }
    }
    return cost;
}

function detailCostCount(rawData) {
    /*
    rawData={
        id: String,
        upgrade : [from, to],
        allSkill : [from, to],
        masterSkill : [
            [from, to],
            [from, to],
            [from, to],
        ],
    }
    */
    console.log(rawData);
    const id = rawData.id;
    let result = [];
    if (!data.hasOwnProperty(id)) {
        console.log('Id doesn\'t exist');
        return []
    }
    if (rawData.hasOwnProperty('upgrade') && rawData.upgrade.length > 0) {
        for (let i = parseInt(rawData.upgrade[0]) + 1; i <= parseInt(rawData.upgrade[1]); i++) {
            let temp = {};
            if (!data[id].evolveCost.hasOwnProperty(i)) {
                continue
            }
            data[id].evolveCost[i].forEach(
                x => {
                    if (!temp.hasOwnProperty(x.id)) {
                        temp[x.id] = x.count;
                    } else {
                        temp[x.id] += x.count;
                    }
                }
            )
            result.push({name: ` ${i}`, cost: temp});
        }
    }
    if (rawData.hasOwnProperty('allSkill') && rawData.allSkill.length > 0) {
        for (let i = parseInt(rawData.allSkill[0]) - 1; i <= parseInt(rawData.allSkill[1]) - 2; i++) {
            let temp = {};
            if (!data[id].allSkillLvlup.hasOwnProperty(i)) {
                continue
            }
            data[id].allSkillLvlup[i].forEach(
                x => {
                    if (!temp.hasOwnProperty(x.id)) {
                        temp[x.id] = x.count;
                    } else {
                        temp[x.id] += x.count;
                    }
                }
            )
            result.push({name: ` ${i + 2}`, cost: temp});
        }
    }
    if (rawData.hasOwnProperty('masterSkill') && rawData.masterSkill.length > 0) {
        rawData.masterSkill.forEach(
            (m, index) => {
                if (data[id].masterSkillCost[index]) {
                    for (let i = parseInt(m[0]); i <= parseInt(m[1]) - 1; i++) {
                        let temp = {};
                        data[id].masterSkillCost[index][i].forEach(
                            x => {
                                if (!temp.hasOwnProperty(x.id)) {
                                    temp[x.id] = x.count;
                                } else {
                                    temp[x.id] += x.count;
                                }
                            }
                        )
                        result.push({
                            name: `${skill_table[data[id].skills[index]].levels[0].name} ${i + 1}`,
                            cost: temp
                        });
                    }
                }
            }
        )
    }
    return result
}

const detailCostRenderStyle = makeStyles(theme => ({
    root:{
        // paddingTop: '10px',
        fontSize:'13px',
    },
    outer:{
        paddingLeft:'80px',
        display: 'flex',
        alignItems:'center',
        borderBottom:'0.5px solid #e0e0e0',
    },
    inner:{
        display: 'flex',
        flexWrap:'wrap',
        margin: '5px 5px 5px 5px',
        '&>div':{
            margin:'0 5px',
        },
    },
    name:{
        width:'160px',
    },
    iconWrap:{
        display:'flex',
        textAlign:'center',
        '& div':{
            lineHeight:'40px',
        }
    }
}));

function DetailCostRender(props) {
    /*
   Render Tree
   1   div
           'name'
   2       div
   3           div span 'item'
    */
    const {rawData,icon}=props;
    const classes = detailCostRenderStyle();
    const detailCost = detailCostCount(rawData);
    return(
        <div className={classes.root}>
            {
                detailCost.map(
                    x => (
                        <div className={classes.outer}>
                            <div className={classes.name}>{ x.name }</div>
                            <div className={classes.inner}>
                                {
                                    Object.keys(x.cost).map(
                                        y=>
                                            <div>{icon?<div className={classes.iconWrap}><ItemIcon style={{height:'40px',width:'40px'}} id={y}/><div>{`x${x.cost[y]}`}</div></div>:(itemData[y].name + ' x'+ x.cost[y])}</div>
                                    )
                                }
                            </div>
                        </div>
                    )
                )
            }
        </div>
    )
}

function costRender(cost) {
    let rarityList = Object.keys(cost).map(key=>[key,itemGet(key,'rarity')]);
    rarityList.sort((a,b)=>b[1]-a[1]);
    return(
        rarityList.map(x=><div>{itemCountStyled(x[0],cost[x[0]])}</div>)
    )
}

const costRenderExStyle = makeStyles(theme => ({
    inner:{
        display: 'flex',
        flexWrap:'wrap',
        margin: '0px 10px 5px 10px',
        '&>div':{
            margin:'0 5px',
        },

    },
    inner_2:{
        display: 'flex',
        flexWrap:'wrap',
        margin: '0px 10px 5px 10px',
        '&>div': {
            display: 'flex',
            flexWrap:'wrap',
        },
        '&>div>div':{
            margin:'0 5px',
        },
    },
    headline:{
        margin:'5px 0',
    },
    tierWrap:{
        display:'flex',
        alignItems:'baseline',
        whiteSpace: 'nowrap',
    },
    tierWrap_icon:{
        display:'flex',
        alignItems:'flex-start',
        whiteSpace: 'nowrap',
        '&>div:first-child':{
            marginTop:'10px',
        }
    },
    iconWrap:{
        textAlign:'center',
    }
}));

function CostRenderEx(props) {
    const {rawCost,icon} = props;
    const classes=costRenderExStyle();
    /*
    Render Tree
    1   div
            ''
    2       div
                'Tier'
    3           div
    4               div span 'item'
    1   div
            ''
    2       div
    3           div
    4               div span 'item'
    1   div
            ''
    2       div
    3           div span 'item'
     */
    if(!rawCost){
        return <div></div>
    }
    let cost;
    let goldCost = 0;
    if(rawCost.hasOwnProperty('result')){
        cost = rawCost.result;
        goldCost = rawCost.goldCost;
        console.log(goldCost);
    }else{
        cost = rawCost;
    }
    const rarityList = [5,4,3,2,1,0];
    const typeList = ['MATERIAL','CHIP','SKILL']
    const sorted = Object.keys(cost).sort((a,b)=>cost[b]-cost[a]);
    const catalog = classifier(sorted,'type',typeList);
    const chipTiers =  classifier(catalog['CHIP'],'rarity',rarityList);
    const tiers = classifier(catalog['MATERIAL'],'rarity',rarityList);

    return(
        [<div>
            <div className={classes.headline}></div>
            {rarityList.map(x=> tiers[x].length>0?(
                <div className={icon?classes.tierWrap_icon:classes.tierWrap}>
                    <div>{`Tier ${x+1}`}</div>
                    <div className={classes.inner}>
                    {tiers[x].map(
                        y=><div>{icon?<div className={classes.iconWrap}><ItemIcon id={y}/>{`x${cost[y]}`}</div>:itemCountStyled(y,cost[y])}</div>
                    )}
                    </div>
                </div>
            ):null)

            }
        </div>,
            (catalog['CHIP'].length>0&&
            <div>
                <div className={classes.headline}></div>
                <div className={classes.inner_2}>
                {
                    rarityList.map(y=> chipTiers[y].length>0?
                        <div>
                            {
                                chipTiers[y].map(
                                    x => <div>{icon ?
                                        <div className={classes.iconWrap}><ItemIcon id={x}/>{`x${cost[x]}`}
                                        </div> : itemCountStyled(x, cost[x])}</div>
                                )
                            }
                        </div>:null)
                }
                </div>
            </div>
            ),
            (catalog['SKILL'].length>0&&
                <div>
                    <div className={classes.headline}></div>
                    <div className={classes.inner}>
                    {
                        sortByRarity(catalog['SKILL']).map(
                            x=><div>{icon?<div className={classes.iconWrap}><ItemIcon id={x}/>{`x${cost[x]}`}</div>:itemCountStyled(x,cost[x])}</div>
                        )
                    }
                    </div>
                </div>
            ),
            (goldCost>0&&
                <div>
                    <div className={classes.headline}></div>
                    <div className={classes.inner}>
                        {`${goldCost}`}
                    </div>
                </div>
            )
        ]
    )
}

function Raw(props) {
    const {classes,id}=props;
    const cost = costCount(id,[0,data[id].maxPhase],[0,6],data[id].masterSkillCost.map(x=>x?[0,3]:[]));
    return(
        <div className={classes.root}>
            <br/>
                {costRender(cost)}
        </div>
)
}



Raw.propTypes = {
    classes: propTypes.object.isRequired,
};

export {costRender, CostRenderEx,costCount,mergeCount, multipleBreakDown, DetailCostRender,detailCostCount};
export default withStyles(styles)(Raw);
